*{
  margin: 0;
  padding: 0;
  font-family: 'Big-John';
}

.App{
    display: flex;
    flex-direction: column;
}
.menu-icon{
    position: fixed;
    left: 2vw;
    top: 2vw;
    z-index: 2000;
    cursor: pointer;
}

.Home{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #fff;
}
.menu-icon img{
    width: 2.5vw;
}
.Title{
    position: absolute;
    width: auto;
    letter-spacing: 8px;
    left: 20vw;
    bottom: 65vh;
    color: #111;
}
.Title h1{
    position: relative;
    font-size: 5vw;
    text-align: right;
    line-height: 0.9;
}
.Title h4{
    font-family: 'Lato';
    font-size: 1.7vw;
    font-weight: 100;
    position: absolute;
    top: 25%;
    right: -4vw;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}
.Logo-Home{
    position: absolute;
    bottom: 40vh;
    left: 10vw;
    -webkit-animation: slide-fwd-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.3s both;
	        animation: slide-fwd-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.3s both;
}
.Btn-Contact{
    position: absolute;
    bottom: 40vh;
    left:30vw;
}
.Dev-img img{
    position: absolute;
    width: 25vw;
    bottom: -5vw;
    right: 10vw;
}
.Down{
    position: absolute;
    width: 4.6vw;
    bottom: 2vw;
    left: 47vw;
}
.Down>a>img{
    width: 4.6vw;
}

/* keyframes */
@-webkit-keyframes slide-fwd-center {
    0% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
    }
    100% {
      -webkit-transform: translateZ(160px);
              transform: translateZ(160px);
    }
  }
  @keyframes slide-fwd-center {
    0% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
    }
    100% {
      -webkit-transform: translateZ(160px);
              transform: translateZ(160px);
    }
  }
  
.Contact{
    position: relative;
    top: 10vh;
}
.Contact a{
    color: #111;
    font-size: 1.6vw;
    font-family: 'Montserrat';
    font-weight: 100;
    padding: 0.4vw 3vw;   
    text-align: center;
    border: 1px solid #333;
    text-decoration: none;
}
.Contact a:hover{
    background: rgba(0,0,0,.1)
}
.Logo{
    position: relative;
    height: auto;
    display: flex;
    flex-direction: row;
 }
.line{
    width: 13vw;
    height: 0.57vw;
    border-radius: 0.295vw;
    background-color: #f9c34c;
    -webkit-transform: rotate(50deg);
            transform: rotate(50deg);
    position: relative;
    left: 7vw;
    top: 4.2vw;
}
.facts{
    font-size: 2vw;
    line-height: 2.7vw;
}
.span1{
    margin-left: 2vw;
}
.span2{
    margin-left:5vw;
}


.Banner{
        position: relative;
        width: 100%;
        height: 100vh;
        z-index: 10;
        background: #fff;
        background-image: url(/static/media/wolf.28b38864.png);
        background-size: 30%;
        background-position-x: 67%;
        background-repeat: no-repeat;
        background-attachment: fixed;
}
.cards{
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translateX(-50%) translateY(-50%);
                transform: translateX(-50%) translateY(-50%);
        height: auto;
        display: flex;
        flex-direction: row;
}
.cards div{
        margin-left: 10%
}
.backg{
        position: absolute;
        width: 100%;
        height: 50%;
        bottom: 0;
        background: #222;
}
.Card{
    position: relative;
    width: 19vw;
    height: 24vw;
    background-color: #f9c34c;
    border-radius: 0.7vw;
}
.Card h1{
    position: relative;
    width: 50%;
    font-size: 2.2vw;
    text-align: center;
    top: 12%;
    left: 24%;
}
.Card h4{
    position: relative;
    width: 50%;
    font-family: 'Lato';
    font-weight: 500;
    font-size: 1.5vw;
    text-align: center;
    top: 15%;
    left: 24%;
}
.Menu{
    position: fixed;
    display: block;
    width: 100%;
    height: 100vh;
    z-index: 1000;
    background: #fff;
    
     -webkit-animation: slide-right 0.8s cubic-bezier(0.860, 0.000, 0.070, 1.000) both;
     animation: slide-right 0.8s cubic-bezier(0.860, 0.000, 0.070, 1.000) both;
}

.Menu img{
    position: absolute;
    width: 25vw;
    left: 50%;
    top: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
}
.Menu ul{
    position: relative;
    top: 40%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    list-style: none;
}
.Menu ul li{
    margin-top: 5vw;
}
.Menu ul li a{
    color: #111;
    text-decoration: none;
    position: relative;
    font-size: 3vw;
    left: 5vw;
}


/* Keyframes */
@-webkit-keyframes slide-right {
    0% {
      -webkit-transform: translateX(-80%);
              transform: translateX(-80%);
    }
    100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }
  @keyframes slide-right {
    0% {
      -webkit-transform: translateX(-80%);
              transform: translateX(-80%);
    }
    100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }
.Mobile{
    width:100%;
    height: 100vh;
    background: #fff;
    position: relative;
}
.wrap-div{
    width: 100%;
    height: 40vh;
    position: absolute;
    top: -15vh;
    background: #222;
    transform:skew(0deg,7deg);
    -ms-transform:skew(0deg,7deg);
    -webkit-transform:skew(0deg,7deg); 
}
.Skills{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #fff;
    display: flex;
    flex-direction: row;
}

/* Skills div */
.skills-div{
    position: relative;
    width: 60%;
    height: 100%;
}
.Rank-div{
    /* editar acá para mover la lista de skill */
    position: absolute;
    bottom: 40%;
}

.profile{
    position: relative;
    width: 40%;
    height: 100%;
    background: #fff;
    background-image: url(/static/media/wolf.28b38864.png);
    background-attachment: fixed;
    background-position-x: 2vw;
    background-position-y: 30%;
    background-repeat: no-repeat;
    background-size: 7vw;
}
.profile img{
    position: absolute;
    width: 50%;
    left: 25%;
    bottom: 50%;
}
.profile h1{
    position: absolute;
    bottom:42%;
    left:33%;
    font-size: 2.2vw;
    text-align: center;
}

.profile p{
    font-family: 'Lato';
    font-weight: 100;
    font-size: 1.5vw;
    text-align: center;
    width: 70%;
    position: absolute;
    bottom: 30%;
    left: 15%;
}
.profile p span{
    font-family: 'Lato';
    font-weight: 100;
    color: #f9c34c;
}
.contact-profile{
    position: absolute;
    bottom: 30%;
    left: 30%;
}

/* skill-component */
.skill-component{
    position: relative;
    bottom:60%;
    left: 10%;
    width: 45vw;
    display: flex;
    flex-direction: row;
    margin: 0.7vw;
  }
  .skill-component h1{
    margin-right: 2vw;
    font-size: 2vw;
  }
  .skill-loader{
    width: 90%;
    position: relative;
    right: 0;
  }
  .skill-loader div{
    background: #333;
    width: 100%;
    height: 0.7vw;
    position: absolute;
    top:45%;
    border-radius: 3.5vw;
  }
  .skill-loader div:nth-child(2){
    background: #f9c34c;
    width: 150px;
  }
  
.Projects{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #fff;
    display: flex;
    flex-direction: column;
}
.Projects-title{
    height: 20%;
}
.Projects-title div{
    position: absolute;
    width: 40%;
    height: 0.6vw;
    left: 30%;
    top: 10vh;
    background: #f9c34c;
    border-radius: 0.3vw;
}
.Projects-title h1{
    font-size: 5vw;
    width: 70%;
    position: absolute;
    top: 5vh;
    letter-spacing: 5px;
    text-align: center;
    left:15%;
}

.Projects-ex{
    height: 80%;
}

/* arrow buttons */
.right-btn, .left-btn{
    position: absolute;
    top:40%;
    cursor: pointer;
    z-index: 10;
    width: 10vw;
    height: 10vw;
    border-radius: 50%;
}
.right-btn{
    left: 7%;
}
.right-btn:hover, .left-btn:hover{
    
    background: rgba(0,0,0,0.2);
    
}
.left-btn{
    right: 7%;
}
.right-btn img, .left-btn img{
    width:10vw;
}
.right-btn img{
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
}

